import React from 'react';

class App extends React.Component {
  componentDidMount() {
    window.initUtil();
    window.initJQuery();
  }

  render() {
    return (
      <div>
        <title>Adrian CASTEJON's Portfolio</title>
        <meta charSet="utf-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1, user-scalable=no" />
        <link rel="stylesheet" href="assets/css/main.css" />
        {/* Header */}
        <div id="header">
          <div className="top">
            {/* Logo */}
            <div id="logo">
              <span className="image avatar48"><img src="images/avatar.jpg" alt="" /></span>
              <h1 id="title">Adrian Castejon</h1>
              <p>Game developer</p>
            </div>
            {/* Nav */}
            <nav id="nav">
              <ul>
                <li><a href="#top" id="top-link"><span className="icon fa-home">Intro</span></a></li>
                <li><a href="#portfolio" id="portfolio-link"><span className="icon fa-th">Portfolio</span></a></li>
                <li><a href="#about" id="about-link"><span className="icon fa-user">About Me</span></a></li>
                <li><a href="#contact" id="contact-link"><span className="icon fa-envelope">Contact</span></a></li>
              </ul>
            </nav>
          </div>
          <div className="bottom">
            {/* Social Icons */}
            <ul className="icons">
              {/* <li><a href="#" class="icon fa-twitter"><span class="label">Twitter</span></a></li> */}
              {/* <li><a href="#" class="icon fa-facebook"><span class="label">Facebook</span></a></li> */}
              <li><a href="https://github.com/Dayuki" className="icon fa-github"><span className="label">Github</span></a></li>
              <li><a href="https://www.linkedin.com/in/adrian-castejon-27406a112/" className="icon fa-linkedin"><span className="label">LinkedIn</span></a></li>
              {/* <li><a href="#" class="icon fa-dribbble"><span class="label">Dribbble</span></a></li> */}
              {/* <li><a href="#" class="icon fa-envelope"><span class="label">Email</span></a></li> */}
            </ul>
          </div>
        </div>
        {/* Main */}
        <div
          id="main"
        >
          {/* Intro */}
          <section id="top" className="one dark cover">
            <div className="container">
              <header>
                <h2 className="alt">
Hi! I'm
                  <strong> Adrian Castejon </strong>
, a student in
                  <br />
                  game design and game programming.
                </h2>
                <p>
I'm looking for new experiences and
                  <br />
                  ways to improve my skills.
                </p>
              </header>
              <footer>
                <a href="#portfolio" className="button scrolly">Show me some of your works !</a>
              </footer>
            </div>
          </section>
          {/* Portfolio */}
          <section id="portfolio" className="two">
            <div className="container">
              <header>
                <h2>Portfolio</h2>
              </header>
              <p>
Here, you can find a bunch of small projects that I led during my studies.
                <br />
Just click on a thumbnail to play the game. (Loading can take a few seconds, do not panic !)
              </p>
              <div className="embedDiv col-12">
                <embed />
              </div>
              <div className="row">
                <div className="col-6 col-12-mobile">
                  <article className="item image fit" data-path="assets/projects/TintBusterForce.swf">
                    <img src="images/TBFCover.PNG" alt="" />
                    <header>
                      <h2>Tint Buster Force</h2>
                      {/* <br><h4>Shoot 'em up with a color-based core mechanic in which you can only hit enemies that match the color of your shoots.</h4> */}
                    </header>
                  </article>
                  <article className="item image fit" data-path="https://cfg-cerise.herokuapp.com/">
                    <img src="images/BWCover.jpg" alt="" />
                    <header>
                      <h2>Blades in the Wind</h2>
                      {/* <br><h4>A 1v1 card-fighting game with a shifumi-like system. Guess your enemy moves and counter them in a clash of swords.<br> Play online with a friend !</h4> */}
                    </header>
                  </article>
                </div>
                <div className="col-6 col-12-mobile">
                  <article className="item image fit" data-path="assets/projects/AcheronEscape/Main.swf">
                    <a href="assets/projects/AcheronEscape.rar" download="AcheronEscape.rar">
                      <img src="images/AECover.PNG" alt="" />
                    </a>
                    <header>
                      <h2>Acheron Escape</h2>
                      {/* <br><h4>Dive into the depths of Acheron and save your soul. Hold to play and dodge the dangers of Hell ! <br> Click to download. </h4> */}
                    </header>
                  </article>
                </div>
              </div>
            </div>
          </section>
          {/* About Me */}
          <section id="about" className="three">
            <div className="container">
              <header>
                <h2>About Me</h2>
              </header>
              <div className="image featured">
                <img src="images/banner.png" alt="" />
                <p>
I am currently pursuing a game design and programming formation at
                  <strong>ISART Digital</strong>
, a school specialized in video games.
                  <br />
Video games are my passion ever since I can remember and I've grown a desire to make my own for many years now.
                  <br />
I am
                  <strong>highly motivated</strong>
                  {' '}
to make my contribution to the ever growing construction which is the video game industry.
                  <br />
I am primarily a programmer and a designer, but I also like to do some graphic developement, mainly pixel art.
                </p>
              </div>
            </div>
          </section>
          {/* Contact */}
          <section id="contact" className="four">
            <div className="container">
              <header>
                <h2>Contact</h2>
              </header>
              <p>
If you have any :
                <br />
- question,
                <br />
- request,
                <br />
- recommendation,
                <br />
- or just a urge to say
                <strong>"Hi !"</strong>
,
                <br />
                {' '}
please feel free to contact me through the form below.
              </p>
              <form id="contact-form" method="post" action="#">
                <div className="row">
                  <div className="col-6 col-12-mobile"><input type="text" name="name" placeholder="Name" /></div>
                  <div className="col-6 col-12-mobile"><input type="text" name="email" placeholder="Email" /></div>
                  <div className="col-12 col-12-mobile"><input type="text" name="object" placeholder="Object" /></div>
                  <div className="col-12">
                    <textarea name="message" placeholder="Message" defaultValue="" />
                  </div>
                  <div className="col-12">
                    <input type="submit" defaultValue="Send Message" />
                  </div>
                </div>
              </form>
            </div>
          </section>
        </div>
        {/* Footer */}
        <div id="footer">
          {/* Copyright */}
          <ul className="copyright">
            <li>© All rights reserved.</li>
            <li>
Design:
              <a href="http://html5up.net">HTML5 UP</a>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}
export default App;
